.app {
  position: fixed;
  top: 50%;
  left: 50%;
}

.message {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.message p {
  text-transform: uppercase;
  color: rgba(239, 83, 80, 1);
  font-family: serif;
  text-align: center;
  font-size: 6rem;
  letter-spacing: 2px;
}

small {
  text-align: center;
  color: rgba(239, 83, 80, 1);
  margin-top: 10px;
  font-size: 1.5rem;
}

.victory p {
  color: rgba(253, 216, 53, 1);
}

.victory small {
  color: rgba(253,216,53 ,1);
}


